:root {
  --body-color: #3E001F;
  --num-color: #F11A7B;
  --text-color: #FFE5AD;
  --btn-color: #982176;
  --main-font: 'Rubik', sans-serif;
}

* {
  box-sizing: border-box;
  margin: 0;
}

body {
  background-color: var(--body-color);
}

.title {
  text-align: center;
  
  color: var(--text-color);
  font-family: var(--main-font);
  border-bottom: 2px solid var(--num-color);
  width: 70%;
  margin: 0 auto;
  padding: 20px;
  margin-bottom: 100px;
  border-bottom-width: 50%;
}

.container {
  margin: auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  height: 60vh;
  border: 2px solid var(--text-color);
  border-radius: 20px;
  width: 70%;
}

.display {
  border: 2px solid var(--btn-color);
  border-radius: 20px;
  width: 50%; 
  max-width: 180px;
  height: 30vh;
  margin: 0 auto;
  text-align: center;
  font-family: var(--main-font);
  color: var(--text-color);
  animation: glow 1s ease-in-out infinite alternate;
}

@keyframes glow {
  from {
    box-shadow: 0 0 0 0 none;
  }
  to {
    box-shadow: 0 0 15px 2px var(--text-color);
  }
}

.value {
  color: var(--num-color);
  font-size: 80px;
  margin-bottom: 20px;
}

.buttons {
  display: flex;
  flex-direction: row; 
  justify-content: space-around;
  align-items: center;
  width: 70%;
  height: 100px;
  margin: 20px auto;
}

button {
  width: 25%; 
  max-width: 150px;
  height: 50px;
  font-size: 25px;
  font-family: var(--main-font);
  border-radius: 100%;
  cursor: pointer;
  background-color: var(--btn-color);
  color: var(--text-color);
  border: 2px solid var(--text-color);
  box-shadow: 0 2px 3px 2px var(--btn-color); 
  transition: all 0.2s ease;
}

button:hover {
  background-color: var(--text-color);
  color: var(--btn-color);
}

button:active {
  border-color: var(--btn-color);
  box-shadow: 0 0 1px 0 var(--btn-color);
}

.cold {
  background-color: rgb(22, 135, 170);
}

.hot {
  background-color: orangered;
}